.contacts {
  display: flex;
  padding-top: 25px;

  align-items: center;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    height: 780px;
    padding-top: 0;
  }

  @media (min-width: @desktop-width) {
    height: 890px;
  }
}

.contacts__header {
  width: 230px;
  margin-bottom: 8px;

  font-size: 24px;

  .font-light();

  line-height: 30px;

  @media (min-width: @tablet-width) {
    width: 385px;
    margin-top: 48px;
    margin-bottom: 15px;
    font-size: 40px;
    line-height: 47px;
  }

  @media (min-width: @desktop-width) {
    width: 500px;
    margin-top: 65px;
    margin-bottom: 35px;

    font-size: 50px;
    line-height: 60px;
  }
}

.contacts__list {
  display: flex;
  width: 100%;
  margin-top: 2px;
  padding: 0;

  align-items: center;
  flex-direction: column;
  list-style-type: none;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    margin-top: 5px;
  }

  @media (min-width: @desktop-width) {
    align-items: space-around;
    max-width: 970px;
  }
}

.contacts__item {
  display: flex;
  width: 215px;
  margin-top: 23px;

  align-items: center;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    width: 320px;
    height: 80px;
    margin-top: 25px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 20px;
  }
}


.contacts__item:nth-child(2) {
  @media (min-width: @desktop-width) {
    order: 1;
  }
}


.contacts__item-header {
  .font-bold();
  margin-bottom: 0;
  font-size: 18px;
}

.contacts__value {
  margin: 0;
  margin-top: 5px;
  padding-top: 0;

  font-size: 24px;
  color: @pink;

  text-decoration: none;

  .font-light();

  @media (min-width: @desktop-width) {
    margin-top: 23px;
  }
}


.contacts__value span {
  @media (min-width: @tablet-width) {
    display: block;
  }
}

.contacts__map {
  position: relative;

  width: 320px;
  margin-top: 15px;

  @media (min-width: @tablet-width) {
    width: 100%;
    margin-top: 20px;
    max-width: 660px;
  }

  @media (min-width: @desktop-width) {
    max-width: 1200px;
  }

  &::before {
    content: "";

    position: absolute;
    top: 0;

    width: 100%;
    height: 100px;
    margin-right: auto;
    margin-left: auto;

    background-image: url("../img/bg-triangle-white-footer.svg");
    background-position: 0% 0%;
    background-repeat: no-repeat;
    background-size: 100%;

    @media (min-width: @tablet-width) {
      height: 200px;
      max-width: 960px;
    }

    @media (min-width: @desktop-width) {
      height: 200px;
      background-position: 0% 0%;

      background-size: 100% 50%;
      max-width: 1200px;
      margin-top: -1px;
    }
  }
}


.contacts__map-waraper {
  display: flex;
  height: 170px;

  overflow-y: hidden;
  justify-content: center;

  @media (min-width: @tablet-width) {
    height: 370px;
  }

  @media (min-width: @desktop-width) {
    height: 500px;
  }
}

.contacts__map-img {
  width: 100%;
  height: 200px;
  border: 0;

  align-self: flex-end;

  @media (min-width: @tablet-width) {
    max-width: 960px;
    height: 400px;
  }

  @media (min-width: @desktop-width) {
    height: 600px;

    max-width: 1200px;
  }
}

.contacts__radio {
  position: absolute;
  top: 45%;
  left: 45%;

  display: block;
  margin: auto;
}
